import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.scss']
})
export class ReportsComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent
  ) { }

  ngOnInit(): void {
  }

  gotoCustomerSales() {
    if (this.vhAuth.checkMyPermission("report_enable_sales_customer")) this.router.navigate(['/sales/dashboard/manage/reports/sales-customer']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoProductSales() {
    if (this.vhAuth.checkMyPermission("report_enable_sales_product")) this.router.navigate(['/sales/dashboard/manage/reports/sales-product'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoEmployeeSales() {
    if (this.vhAuth.checkMyPermission("report_enable_sales_employee")) this.router.navigate(['/sales/dashboard/manage/reports/sales-employee']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoCategory() {
    if (this.vhAuth.checkMyPermission("report_enable_sales_category")) this.router.navigate(['/sales/dashboard/manage/reports/sales-category']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoServiceSales() {
    if (this.vhAuth.checkMyPermission("report_enable_sales_service")) this.router.navigate(['/sales/dashboard/manage/reports/sales-service']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoComboSales() {
    if (this.vhAuth.checkMyPermission("report_enable_sales_combo")) this.router.navigate(['/sales/dashboard/manage/reports/sales-combo'])
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoLowStock() {
    if (this.vhAuth.checkMyPermission("report_enable_out_of_stock")) this.router.navigate(['/sales/dashboard/manage/reports/out-of-stock']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoSalesProfit() {
    if (this.vhAuth.checkMyPermission("report_enable_sales_profit")) this.router.navigate(['/sales/dashboard/manage/reports/sales-profit']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoBestSellingProduct() {
    if (this.vhAuth.checkMyPermission("report_enable_top_product")) this.router.navigate(['/sales/dashboard/manage/reports/best-selling-product']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoBestSellingCategory() {
    if (this.vhAuth.checkMyPermission("report_enable_top_category")) this.router.navigate(['/sales/dashboard/manage/reports/best-selling-category']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoInputOutputReturn() {
    if (this.vhAuth.checkMyPermission("report_enable_in_out_return")) this.router.navigate(['/sales/dashboard/manage/reports/input-output-return']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoInputOutputInventory() {
    if (this.vhAuth.checkMyPermission("report_enable_in_out_inventory")) this.router.navigate(['/sales/dashboard/manage/reports/input-output-inventory']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  goToBranchesComparison() {
    if (this.vhAuth.checkMyPermission("report_enable_branches_comparison")) this.router.navigate(['/sales/dashboard/manage/reports/branches-comparison']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoProfitProduct() {
    if (this.vhAuth.checkMyPermission("report_enable_product_profit")) this.router.navigate(['/sales/dashboard/manage/reports/profit-product']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoSalesVoucher(){
    if (this.vhAuth.checkMyPermission("report_enable_sales_voucher")) this.router.navigate(['/sales/dashboard/manage/reports/sales-voucher']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  gotoSalesCard(){
    if (this.vhAuth.checkMyPermission("report_enable_sales_payment_card")) this.router.navigate(['/sales/dashboard/manage/reports/sales-payment-card']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
}
